@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
  text-decoration: none;
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
  line-height: 1.1;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

:root {
  --head-font: #20292f ;
  --head-bg: #fff;
  --head-hover: #20292f;
}

/*@function rem($pixels, $context: 16) {
    @return (math.div($pixels, $context)) * 1rem;
}*/
.header {
  display: flex;
  max-width: 100%;
  padding: 0 2.5rem;
  justify-content: space-between;
  color: var(--head-font);
  font-display: swap;
  background-color: var(--head-bg);
  align-items: center;
}
.header__brandlogo__title {
  font-size: 2rem;
  font-weight: 600;
  margin: 0;
}
.header__navmenu {
  display: flex;
  flex-direction: column;
  list-style-type: none;
}
@media (min-width: 40em) {
  .header__navmenu {
    font-size: 0.9375rem;
    flex-direction: row;
  }
}
.header__navmenu__container {
  padding-right: 1.375rem;
  font-weight: 500;
}
.header__navmenu__container:hover {
  color: var(--head-hover);
}

